import * as acttiontypes from "./actiontypes";
export const LOGINSUCCESS = (response) => {
  return {
    type: acttiontypes.LOGINSUCCESS,
    payload: response,
  };
};

export const LOGINOUTUSER = () => {
  return {
    type: acttiontypes.LOGINOUTUSER,
  };
};
