import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import { CircularProgress } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",

    justifyContent: "center",
  },
  paper: {
    backgroundColor: "#fff",
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function TransitionsModal(props) {
  const classes = useStyles();
  const { open, handleClose } = props;
  const [loading, setLoading] = React.useState(false);
  const HandleSubmit = () => {
    setLoading(true);
  };
  return (
    // <div>
    //   <button type="button" onClick={handleOpen}>
    //     react-transition-group
    //   </button>
    <Modal
      aria-labelledby="transition-modal-title"
      aria-describedby="transition-modal-description"
      className={classes.modal}
      open={open}
      onClose={handleClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
    >
      <Fade in={open}>
        <div
          style={{ height: "80vh", marginTop: "80px", overflowY: "scroll" }}
          className={`${classes.paper} col-10`}
        >
          <h2 style={{ color: "dodgerblue" }}>Add Insurers</h2>
          <p id="transition-modal-description">
            Here You Can Add insurance comapanies to Data base
          </p>
          <div style={{ height: "40px" }} />
          <div className="row editInsurer justify-content-center">
            <div className="col-lg-6">
              <h6
                className="btn3"
                style={{ backgroundColor: "dodgerblue", color: "#fff" }}
              >
                Details:
              </h6>
              <div style={{ height: "40px" }} />
              <label>Insurer Name:</label>
              <input placeholder="Insurer Name" className="input-cars" />
              <label>Trade Name:</label>
              <input placeholder="Trade Name" className="input-cars" />
              <label>Year Established:</label>
              <input placeholder="" className="input-cars" />
              <label>Website Url:</label>
              <input placeholder="" className="input-cars" />
              <label>telephone:</label>
              <input
                placeholder=" telephone number of insurere"
                className="input-cars"
              />{" "}
              <label>Classification:</label>
              <input placeholder="" className="input-cars" />
            </div>
            <div className="col-lg-6">
              <label>Registration Number:</label>
              <input placeholder="" className="input-cars" />
              <label>ThumbNail Image:</label>
              <input
                accept="image/png, image/jpeg"
                style={{ fontSize: "10px" }}
                type="File"
                placeholder=""
                className="input-cars"
              />
              <div style={{ height: "40px" }} />
              <h6
                className="btn3"
                style={{ backgroundColor: "dodgerblue", color: "#fff" }}
              >
                Insurer Quotes:
              </h6>
              <div style={{ height: "40px" }} />
              <em style={{ color: "tomato" }}> Prices In Naira</em>
              <div className="row">
                <div className="col-4">
                  <label>Car Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Health Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Travel Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Home Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Bike Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Pc Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Laptop Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Ipod Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Tablets Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Phone Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Digi. Cam. Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
                <div className="col-4">
                  <label>Tricyc. Quote Price:</label>
                  <input type="number" placeholder="" className="input-cars" />
                </div>
              </div>
            </div>
          </div>
          <div style={{ height: "40px" }} />
          <div className="text-center">
            <button onClick={HandleSubmit} className="btn1">
              {loading ? (
                <CircularProgress
                  size={20}
                  style={{ color: "#fff", marginRight: "10px" }}
                />
              ) : (
                "Submit"
              )}
            </button>
          </div>
          <div style={{ height: "40px" }} />
        </div>
      </Fade>
    </Modal>
  );
}
