import React from "react";
import AddCircleOutlineIcon from "@material-ui/icons/AddCircleOutline";
import TransitionsModal from "./entityModal";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

const AdminInsuranceEntities = () => {
  const [open, setOpen] = React.useState(false);
  const [openAlert, setOpenAlert] = React.useState({
    status: false,
    message: "",
  });
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const handlesetOpen = (status, message) => {
    setOpenAlert({ ...openAlert, status, message });
  };
  function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }
  return (
    <div className="container-fluid">
      <div className="row page-title">
        <div className="col-md-12">
          <nav aria-label="breadcrumb" className="float-right mt-1">
            <ol className="breadcrumb">
              <li className="breadcrumb-item">
                <a href="/">IPolicyMart</a>
              </li>
              <li className="breadcrumb-item">
                <a href="/users-insuranceEntiries">Admin -Insurers</a>
              </li>
            </ol>
          </nav>
          <h3 className="mb-1 mt-0">
            <b> Manage Insurers </b>
          </h3>
        </div>
      </div>
      <Snackbar
        anchorOrigin={{
          vertical: "center",
          horizontal: "center",
        }}
        open={openAlert.status}
        autoHideDuration={6000}
        onClose={() => {
          handlesetOpen(false, "");
        }}
      >
        <Alert
          onClose={() => {
            handlesetOpen(false, "");
          }}
          severity="success"
        >
          {openAlert.message}
        </Alert>
      </Snackbar>
      <TransitionsModal handleClose={handleClose} open={open} />
      <div className="row justify-content-end">
        <div style={{ float: "right" }} className="col-2">
          <button onClick={handleOpen} className="btn1">
            Add <AddCircleOutlineIcon />
          </button>
        </div>
      </div>

      <div style={{ MaxWidth: "100%", overflowX: "scroll" }} className="row">
        <div className="col-12">
          <table class="styled-table">
            <thead>
              <tr>
                <th>Name</th>
                <th>Trade Name</th>

                <th>Developer Portal</th>
                <th>Car Insurance Price</th>
                <th>Bike Insurance Price</th>
                <th>Tricy. Insurance Price</th>
                <th>Home Insurance Price</th>
                <th>Life Insurance Price</th>
                <th>Travel Insurance Price</th>
                <th>Gadget Insurance Price</th>
                <th>Health Insurance Price</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Aiico insurance Plc</td>
                <td>Aiico insurance Plc</td>
                <td>Dom</td>
                <td>6000</td>
                <td>Dom</td>
                <td>6000</td>
                <td>Dom</td>
                <td>6000</td>
                <td>6000</td>
                <td>Dom</td>
              </tr>
              <tr class="active-row">
                <td>Melissa</td>
                <td>5150</td>
                <td>Melissa</td>
                <td>5150</td>
                <td>Melissa</td>
                <td>5150</td>
                <td>Melissa</td>
                <td>5150</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default AdminInsuranceEntities;
