import React from "react";
import { useHistory } from "react-router-dom";
import Alert from "@material-ui/lab/Alert";
import { useDispatch, useSelector } from "react-redux";
import { SETCLAIMPROCESS } from "../../../redux/action";

const Claimsstep2 = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const handleBack = () => history.goBack();
  const [policyNumber, setpolicyNumber] = React.useState("");

  const [errorAlert, setErrorAlert] = React.useState({
    status: false,
    message: "",
  });
  const handleNext = () => {
    if (!policyNumber) {
      return setErrorAlert({
        status: true,
        message: "Provide policy number",
      });
    }
    dispatch(SETCLAIMPROCESS({ policyNumber: policyNumber }));
    history.push("/insurance-claims3");
  };
  return (
    <div className="container">
      <div className="col-lg-9">
        <h5>Insurance Details</h5>
        <div style={{ height: "20px" }} />
        <p className="midtext1">What Is Your Policy Number:</p>
        <div className="col-lg-4">
          {errorAlert.status ? (
            <Alert style={{ marginBottom: "10px" }} severity="error">
              {errorAlert.message}
            </Alert>
          ) : null}
        </div>
        <input
          onChange={(e) => setpolicyNumber(e.target.value)}
          value={policyNumber}
          className="input-cars"
          placeholder="Policy Number"
        />
        <div style={{ height: "20px" }} />
        <button onClick={handleBack} className="btn2 ">
          back
        </button>
        <button onClick={handleNext} className="btn1 carNextbtn">
          Next
        </button>
      </div>
    </div>
  );
};

export default Claimsstep2;
